*{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}

.header{
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    align-items: center;
    margin-left: 30px;
    margin-right: 30px;
    border-bottom: solid 1px rgba(128, 128, 128, 0.336);
}
.main__list{
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   flex-wrap: wrap;
   margin: 20px;
   
   .main__list--item{
    display: flex;
    flex-direction: column;
    border: solid 1px rgba(128, 128, 128, 0.336);
    border-radius: 10px;
    padding: 10px;
    height: 90px;
    width: 300px;
    margin: 25px;
    }
    .fas{
        color: orange;
        position: relative;
        top: -35px;
        left: 286px;


    }
}
.main__title{
    margin: 30px;
    border-bottom: 1px solid rgba(128, 128, 128, 0.336);
}
.form{
    margin: 30px;
    display: flex;
    flex-direction: column;
    .label__format{
        flex-direction: row;
        justify-content: flex-start;
        .label{
            float: left;

        }
       
    }
   
}
.form__btn{
    width: 200px;
    height: 30px;
    border-radius: 15px;
    background-color: rgb(139, 206, 39);
    border: none;
    font-weight: bolder;
    color: lightgoldenrodyellow;
}
